import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/iconfont/iconfont.css'
import './assets/iconfont/iconfont.js'
import store from './store/index.js'
import './assets/style/reset.css'
import './assets/style/sass.scss';
import 'bootstrap/dist/js/bootstrap.min.js'
import {Message} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import axios from './http/axios.js'
//全局引入element-ui
// import Element from 'element-ui'
// Vue.use(Element)

//在调用 Vue.use 前，给 Message 添加 install 方法
Message.install = function (Vue, options) {
  Vue.prototype.$message = Message
}

Vue.use(Message)
Vue.config.productionTip = false
Vue.prototype.$message1 = function(msg, state) {
	this.$message({
		message: msg,
		duration: 1000,
		type: state
	})
}



new Vue({
	el: '#app',
	router,
	store,
	components: {
		App
	},
	template: '<App/>'
})
console.log('main')

//背景效果
$(document).on('mousemove', (evt) => {
	$('body')
		.css('background-position-x', Math.ceil(evt.pageX / 40))
		.css('background-position-y', Math.ceil(evt.pageY / 40));
});
//移动端改变手机的旋转方向?
window.ondeviceorientation = (evt) => {
	$('body')
		.css('background-position-x', evt.gamma)
		.css('background-position-y', evt.beta);
};


//转成日期形式
function add0(m) {
	return m < 10 ? '0' + m : m
}

function changeTime(shijianchuo) {
	//shijianchuo是整数，否则要parseInt转换
	var time = new Date(shijianchuo);
	var y = time.getFullYear();
	var m = time.getMonth() + 1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();
	var s = time.getSeconds();
	return y + '-' + add0(m) + '-' + add0(d) + ' ' + add0(h) + ':' + add0(mm) + ':' + add0(s);
}
export {
	changeTime
}



