import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import qqlogin from '@/components/qqlogin'

Vue.use(Router)

export default new Router({
	mode:'history',
	routes: [{
			path: '/',
			name: 'Home',
			component: Home
		},
		{
			path: '/memo/qqlogin',
			name: 'qqlogin',
			component: qqlogin
		}
	]
})



